<template>
  <div id="cssTest" class="topTitle">
    안녕 이라 말하지마...
  </div>

  <div>
    <a href="https://testnets.opensea.io/collection/kangnengtest">강냉쓰 테스트 오픈씨</a>
  </div>
  <div class="hello">
    <div v-if="isInstallMetaMask == true">
    메타 마스크 설치됨
    <!--메타 마스크 연결 버튼-->
    <div v-if="currentAddress == null">
      <button v-on:click="connectMetaMask">메타 마스크 연결</button>
    </div>

    <div v-if="currentAddress != null">
      현재 계정 주소 : {{currentAddress}}
      <!-- 컨트렉트 연결 버튼 -->
      <div v-if="connectedContract == null">
        <button v-on:click="connectContract">컨트랙트 연결</button>
      </div>
      <div v-else>
        <!-- owner of 함수를 호출해보자.-->
        <div>
          <button v-on:click="ownerOf">owner Of</button>
          <button v-on:click="balanceOf">banace Of</button>
          <button v-on:click="getApprove">get Approve</button>
          <button v-on:click="GMint">Mint</button>
          <div>
            <input type="text" placeholder="tokenId" v-model="ownerOf_tokenId">
          </div>
          <div>
            <button v-on:click="GMint">GMINT 어떤 결과가 일어날지 모릅니다...</button>
            <input type="text" placeholder="to" v-model="mint_to">
            <input type="text" placeholder="tokenId" v-model="mint_tokenId">
            <input type="text" placeholder="uri" v-model="mint_url">
          </div>
          <div>
            <button v-on:click="mint">mint</button>
            <input type="text" placeholder="to" v-model="mint_to"/>
            <input type="text" placeholder="tokenId" v-model="mint_tokenId"/>
            <input type="text" placeholder="uri" v-model="mint_uri"/>
            <div v-if="mint_result != null">
               mint : {{mint_result}}
            </div>
          </div>
        </div>
      </div>
    </div>
    </div>
    <div v-else>메타 마스크 설치 안됨.</div>
  </div>
</template>

<script>
import {ethers} from 'ethers';
import abi from '../assets/KNToken_ABI.json';

export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      isInstallMetaMask : window.ethereum != null && window.ethereum.isMetaMask == true,
      currentAddress : null,
      connectedContract : null,

      // owner of 에 필요한 변수
      ownerOf_tokenId : null,
      ownerOf_result : null,
      balanceOf_result : null,
      approveAddress_result : null,

      mint_to : null,
      mint_tokenId : null,
      mint_uri : null,
     mint_result : null,
    }
  },
  methods:{
    async connectMetaMask()
    {
      console.log("connectMetaMast 호출")
      var account = await window.ethereum.request( {method:'eth_requestAccounts'})
      console.log(account)
      if (account.length > 0)
      {
        this.currentAddress = account[0]
        console.log(this.currentAddress)
      }else
      {
        console.log("계정 정보가 존재하지 않음.")
      }
    },
    async connectContract()
    {
      console.log("connect Contract 호출")
      var contractAddress = "0xA78aA2E43B41AE39a35E87A72398337846b58Afd"
      var provider = new ethers.providers.Web3Provider(window.ethereum)
      var tempSigner = provider.getSigner()
      var connectedContract = new ethers.Contract(contractAddress,abi,tempSigner)
      console.log(connectedContract)
      this.connectedContract = connectedContract
    },
    async ownerOf()
    {
      console.log("owner of 호출")
      var ownerAddress = await this.connectedContract.ownerOf(Number(this.ownerOf_tokenId))
      console.log(ownerAddress)
      this.ownerOf_result = ownerAddress
    },
    async balanceOf()
    {
      console.log("balance of 호출")
      var balanceCount = await this.connectedContract.balanceOf(this.ownerOf_result)
      console.log(balanceCount)
      this.balanceOf_result = balanceCount
    },
    async getApprove()
    {
      console.log("balance of 호출")
      var ApproveAddress = await this.connectedContract.getApproved(Number(this.ownerOf_tokenId))
      console.log(ApproveAddress)
      this.approveAddress_result = ApproveAddress
    },
    async GMint()
    {
      console.log("mint 호출")
      var targetAddress = null
      if (this.ownerOf_result != null)
        targetAddress = this.ownerOf_result
      else
        targetAddress = this.currentAddress
      var urlString = "ipfs://Qmb5CLucFodZbJUh8A2LGGitrGuA5esHX61d9YbJa7jftp/1.json"
      await this.connectedContract.mint(targetAddress,Number(this.ownerOf_tokenId),urlString)
      console.log(this.ownerOf_tokenId, "번 토큰을 ", this.currentAddress, "에 Mint!")
    },
    async mint()
    {
      console.log("mint 함수 호출")
      try
      {
        var transaction = await this.connectedContract.mint(
                                  this.mint_to, 
                                  this.mint_tokenId, 
                                  this.mint_uri);
        console.log(transaction);
        this.mint_result = "생성 대기중";
        await transaction.wait();
        
        this.mint_result = transaction;
      }
      catch(err)
      {
        console.log(err);
        this.mint_result = err;
        console.log("실패함")
      }
    }
  }
}
</script>

